import { Component, Input, OnInit } from "@angular/core";

@Component({
    selector: 'set-columns',
    templateUrl: './setcolumns.component.html'
})
export class SetColumnsComponent implements OnInit {

    @Input() data = [];
    @Input() checkedItems = [];

    selectedIds: string[] = []
    constructor() {}

    ngOnInit() {
        this.selectedIds = (this.checkedItems || []).map(n => n.id);
        if (this.data) {
            this.data.forEach(item => {
                if (!item.id) {
                    item.id = item.field;
                }
            })
        }
    }
}