import { cloneDeep, flatten } from "lodash-es";


function _setColumnId(c) {
    c.id = c.id || c.field;
    if (c.colspan && c.colspan > 1) {
        c.groupHeader = true;
        c.id = 'groupHeader_'+ c.id;
    }
}


function _columnsToTreeNodes2(node, cols) {
	if (node.data.colspan) {
        if (cols[node.level + 1] && cols[node.level + 1].length) {
            var i = 0;
            do {
                let col = cols[node.level + 1].splice(0, 1)[0];
                i += col.colspan || 1;
                _setColumnId(col);
                const child = { data: col, children: [], level: node.level + 1, expanded: true, visible: true };
                node.children.push(child);

                if (col.colspan && col.colspan > 1) {
                    _columnsToTreeNodes2(child, cols);
                }

            } while(i< node.data.colspan && cols[node.level + 1].length)
        }
    }
}

/**  */
export function columnsToTreeNodes(cols) {
	var nodes = [];
	cols[0].filter(n => n.field !== '_datagrid-setting-control_').forEach(c => {
        _setColumnId(c);
	    var node = { data: c, children: [], level: 0, expanded: true, visible: true };
	    nodes.push(node);
	});
    nodes.forEach(n => {
    	_columnsToTreeNodes2(n, cols);
    });

    return nodes;
}





// 
export const reset2DefaultForMultiHeaders = (gridInstance) => {
    const options = gridInstance.dfs['_state'].initialOptions;
    const settings: any = {
        viewColumns: [],
        columnFormat: []
    };

    if (options.sort) {
        const sortOrders = options.sort.sortOrder? options.sort.sortOrder.split(',') : [];
        const sortNames = options.sort.sortName ? options.sort.sortName.split(',') : [];
        settings.sortInfo = { sortName: sortNames, sortOrder:  sortOrders };
    } else {
        settings.sortInfo = {};
    }
    settings.groupField =  options.groupField ? options.groupField.split(',') : [];
    settings.expandGroupRows = !!options.expandGroupRows;

    settings.multiHeaders = {
        treeData: null,
        viewCols: []
    };

    return {
        settings,
        multiHeaderOptions: {
            columns: options.groupHeaderFields,
            treeData: null
        }
    }
}

/**   */
export function mergetColumnOptions(viewCols: any, gridInitOptions: any) {
    if (viewCols && gridInitOptions) {
        const dataFields = flatten(gridInitOptions['designerColumns']);
        const _cols = cloneDeep(viewCols).map((cols: any) => {
            return cols.map((col: any) => {

                if (!col.field || col.field.indexOf('farris-datagrid-column_') > -1) {
                    col.filter = false;
                }

                if (!col.colspan || col.colspan === 1) { // 
                    const old = dataFields.find((n: any) => n.field === col.field || n.field === col.id);
                    if (old) {
                        const _col = Object.assign({},old, col);
                        if (!col.colspan) {
                            delete _col.colspan;
                        }
                        if (!_col.rowspan) {
                            delete _col.rowspan;
                        }

                        return _col;
                    }
                }
                return col;
            }).filter(n => n)
        });

        return _cols;
    }
}


export function refreshGridColumns(gridInstance: any, newColumns: any) {
    if(!gridInstance) {
        return;
    }
    const gridInitOptions = gridInstance.dfs['_state'].initialOptions;
    if (newColumns && gridInitOptions) {
        gridInstance.columns = mergetColumnOptions(newColumns, gridInitOptions)
    }
}

/**  */
export function  mergeSortInfo(newColumns: any, sortInfo: any) {
    if (sortInfo && Object.keys(sortInfo).length) {
        const { sortName, sortOrder } = sortInfo;
        if (sortName && sortName.length) {
            newColumns.forEach((cols: any) => {
                cols.forEach((col: any) => {
                    const i = sortName.indexOf(col.field);
                    if (i > -1) {
                        col.sortable = true;
                        col.order = sortOrder[i] || 'asc';
                    } else {
                        col.order = '';
                    }
                })
            })
        }
    }
}


export function multiHeadersHasDataFields(multiHeaderColumns: any) {
    if (multiHeaderColumns && multiHeaderColumns.length) {
        return flatten(multiHeaderColumns).filter((n: any) => n.field &&( !n.colspan || n.colspan === 1) ).length;
    }
    return false;
}


export function getDataFieldsForMultiHeaders(groupHeaderCols) {
    return flatten(groupHeaderCols).filter((n: any) => n.field && ( !n.colspan || n.colspan === 1));
}


export function cleanGroupFields(viewCols: any, groupFields: any) {
    const cols: any = getDataFieldsForMultiHeaders(viewCols);
    const groupColumns = groupFields.map( f => {
        const col = cols.find(col => col.field === f);
        return col;
    }).filter(n => n);

    return groupColumns.map(n => n.field);
}


export function cleanSortFields(viewCols: any, sortInfo: { sortName: [], sortOrder: [] }) {
    const cols: any = getDataFieldsForMultiHeaders(viewCols);
    const sortNames = sortInfo.sortName.map( (f, i) => {
        const col = cols.find(col => col.field === f);
        if (!col) {
            sortInfo.sortOrder.splice(i, 1);
        }
        return col;
    }).filter(n => n);

    return {
        sortName: sortNames.map(n => n.field),
        sortOrder: sortInfo.sortOrder
    };
}