import { AfterViewInit, ChangeDetectorRef,EventEmitter, Component, ElementRef, Injector, Input, NgZone, OnChanges, OnDestroy, OnInit, Output, Renderer2, SimpleChanges, TemplateRef, ViewChild, ViewEncapsulation } from "@angular/core";

import { of } from "rxjs";

import { BsModalService } from "@farris/ui-modal";
import { RowNode, TreeNode, TreeTableComponent } from "@farris/ui-treetable";
import { OverLayHiddenService } from '@farris/ui-common';
import { NotifyService } from '@farris/ui-notify';
import { DatagridComponent, DataColumn } from '@farris/ui-datagrid';

import { NewHeaderFormComponent } from "./new-header.component";
import { SetColumnsComponent } from "./setcolumns.component";
import { columnsToTreeNodes } from "../mutli-headers.helper";
import { cloneDeep, flatten } from "lodash-es";
import { ColumnTreeOptions } from "../setting.model";
import { LocaleService } from "@farris/ui-locale";


@Component({
    selector: 'column-tree',
    styleUrls: ['./column-tree.scss'],
    templateUrl: './column-tree.component.html',
    exportAs: 'groupHeaderTree',
    encapsulation: ViewEncapsulation.None
})
export class ColumnTreeComponent implements OnInit, OnDestroy, AfterViewInit, OnChanges {

    @Input() dg: DatagridComponent;
    @Input() options: Partial<ColumnTreeOptions> = {};

    @Input() inModal = false;

    @Output() nodeSelected = new EventEmitter<any>();
    
    @ViewChild('nodeTemplate') cellTemp: TemplateRef<any>
    @ViewChild('tt') tt: TreeTableComponent;
    
    fields = [];
    treecols = [];
    treeData = [];
    contextMenus = [];

    /**  __HOU_XUAN_LIE_ID__ */
    HOU_XUAN_LIE_ID = '__HOU_XUAN_LIE_ID__';


    GROUP_HEADER_ICON = 'f-icon f-icon-file-folder-close';

    Hou_Xuan_Qu = { id: this.HOU_XUAN_LIE_ID, title: '()', houxuanquNode: true };


    languageRes = {
        title: '',
        preview: '',
        tips: '; ',
        contextMenus: {
            new: '',
            newChild: '',
            setDataField: '',
            rename: '',
            top: '',
            up: '',
            down: '',
            bottom: '',
            cancelGroup: '',
            remove: ''
        },
        candidate: '()',
        setHeader: '',
        ok: '',
        cancel: '',
        required: '',
        defaultGroupHeaderName: '1',
        newHeaderPlaceholder: '',
        newHeaderLabel: ''
    }

    private localeSer: LocaleService = null;
    constructor(private injector: Injector, private ngzone: NgZone, private cd: ChangeDetectorRef, private render: Renderer2,
        private modalService: BsModalService, private el: ElementRef,
        private notitySer: NotifyService) {
            this.localeSer = this.injector.get(LocaleService, null);
        }

    ngOnInit(): void {
        const groupHeaders = this.dg.dfs['_state'].initialOptions.groupHeaderFields;
        const allDataFields = flatten<DataColumn>(groupHeaders).filter((col: DataColumn) => !col.colspan || col.colspan == 1);
        this.fields = allDataFields.filter(n => n.field && n.field !== this.dg.ControlPanelFeild);

        // 
        this.treecols = [
            { field: 'title', title: '', template: this.cellTemp }
        ];

        this.languageRes = this.localeSer.getValue('headerGroup');
        this.Hou_Xuan_Qu.title = this.languageRes.candidate;

        this.initTreeData();

        this.initTreenodeContextMenus();
        this.listenTreeTableEvent();
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.options && !changes.options.isFirstChange()) {
            this.initTreeData();
            this.tt.clearSelections();
        }
    }

    ngAfterViewInit(): void {
    }

    ngOnDestroy(): void {
    }

    private listenTreeTableEvent() {
        this.tt.removed.subscribe(rowNode => {

            if (rowNode && rowNode.parent) {
                rowNode.parent.leaf = !(rowNode.parent.children && rowNode.parent.children.length);
                if (rowNode.parent.leaf) {
                    rowNode.parent.icon = this.GROUP_HEADER_ICON;
                }
            }

            if (!rowNode.node.data.groupHeader) {
                const hxl = this.getHouXuanNode();

                if (!rowNode.parent) {
                    this.treeData = this.treeData.filter(n => n.data.id !== rowNode.id);
                }

                this.tt.append(rowNode.node.data, hxl.node);
            }

          
        });
    }

    private initTreenodeContextMenus() {
        this.contextMenus = [
            { id: 1, title: this.languageRes.contextMenus.new, icon: 'f-icon f-icon-add', 
                handle: (t) => {
                    // console.log(t);
                    this.addNewGroup(t.data, false);
                }
            },
            { id: 2, title: this.languageRes.contextMenus.newChild, visible: (t: any) => {
                    return this.isGroupHeader(t.data) && t.data.level + 1 < this.dg.maxMultiHeaders - 1;
                },
                handle: (t) => {
                    this.addNewGroup(t.data, true);
                }
            },
            { id: 3, title: this.languageRes.contextMenus.setDataField, visible: (t: any) => {return this.isGroupHeader(t.data) },
                handle: (t) => {
                    this.setColumnsForGroupHeader(null, t.data);
                }
            },
            { id: 31, title: this.languageRes.contextMenus.rename, icon: 'f-icon f-icon-edit', 
                handle: (t) => {
                    this.resetHeaderName(null, t.data);
                }
            },
            '-',
            {
                id: 4, title: this.languageRes.contextMenus.top, icon: 'f-icon f-icon-roofing', 
                visible: (t: any) => {
                    return this.moveMenuItemVisiblily(t.data);
                },
                handle: (t) => {
                    this.moveTop(null, t.data);
                }
            },
            {
                id: 5, title: this.languageRes.contextMenus.up, icon: 'f-icon f-icon-arrow-chevron-up',
                visible: (t: any) => {return this.moveMenuItemVisiblily(t.data)},
                handle: (t) => {
                    this.moveUp(null, t.data);
                }
            },
            {
                id: 6, title: this.languageRes.contextMenus.down, icon: 'f-icon f-icon-arrow-chevron-down',
                visible: (t: any) => {return this.moveMenuItemVisiblily(t.data)},
                handle: (t) => {
                    this.moveDown(null, t.data);
                }
            },
            {
                id: 7, title: this.languageRes.contextMenus.bottom, icon: 'f-icon f-icon-bottomsetting',
                visible: (t: any) => {return this.moveMenuItemVisiblily(t.data)},
                handle: (t) => {
                    this.moveBottom(null, t.data);
                }
            },
            '-',
            {
                id: 8, title: this.languageRes.contextMenus.remove, icon: 'f-icon f-icon-minus',
                visible: (t) => {
                    return !t.data.node.data.houxuanquNode && !t.data.node.data.groupHeader;
                },
                handle: (t) => {
                    this.tt.remove(t.data.id);
                }
            },
            {
                id: 9, title: this.languageRes.contextMenus.cancelGroup, icon: 'f-icon f-icon-x',
                visible: (t) => {
                    return t.data.node.data.groupHeader;
                },
                handle: (t) => {
                    this.removeGroup(null, t.data);
                }
            }
        ];
    }

    /**  */
    moveMenuItemVisiblily = (t: RowNode) => {
        if (t) {
            const rowData = t.node.data;
            if (rowData) {
                return rowData['id'] != this.HOU_XUAN_LIE_ID;
            }
            return true;
        }
        return true;
    }

    isGroupHeader = (t: RowNode) => {
        if (t) {
            const rowData = t.node.data;
            if (rowData) {
                return !!rowData['groupHeader'];
            }
            return true;
        }
        return true;
    }

    initTreeData() {

        if (this.options) {
            const { treeData } = this.options;

            if (treeData) {
                this.treeData = treeData;
                return;
            }
        }

        if (this.options.columns && this.options.columns.length > 1) {
            const _treeData = columnsToTreeNodes(cloneDeep(this.options.columns));
            const houXuanLie = this.fields.filter(f => !flatten(this.options.columns).find(col => col.field === f.field)).map((a:any) => {
                return {
                    data: a,
                    children: [],
                    visible: true
                }
            });
            _treeData.push({
                data: this.Hou_Xuan_Qu,
                children: houXuanLie,
                expanded: true
            });
            this.treeData = _treeData;
        } else {
            this.treeData = [{
                data: { id: 'groupHeader_1', title: this.languageRes.defaultGroupHeaderName, halign: 'left', groupHeader: true },
                children: this.fields.map((n, i) => {
                    n['index'] = i;
                    n['id'] = n['field'];
                    return {
                        data: n,
                        children: [],
                        visible: true
                    };
                }),
                icon: this.GROUP_HEADER_ICON,
                expanded: true, visible: true
            }, {
                data: this.Hou_Xuan_Qu,
                children: [],
                expanded: true,
                icon: this.GROUP_HEADER_ICON
            }];
        }
    }

    private getHouXuanNode() {
        return this.tt.findRowNode(this.HOU_XUAN_LIE_ID);
    }

    onTreeSelected($event) {
        this.nodeSelected.emit($event);
    }

    private showNewHeaderDialog(okCallback, dlgTitle) {
        const modalRef = this.modalService.show(NewHeaderFormComponent, {
            title: dlgTitle,
            width: 340,
            height: 190,
            showMaxButton: false,
            resizable: false,
            initialState: {
                title: '',
                placeholder: this.languageRes.newHeaderPlaceholder,
                label: this.languageRes.newHeaderLabel
            },
            buttons: [
                {
                    text: this.languageRes.cancel,
                    cls: 'btn btn-secondary',
                    handle: () => {
                        modalRef.close();
                    }
                },
                {
                    text: this.languageRes.ok,
                    cls: 'btn btn-primary',
                    handle: okCallback
                }
            ]
        });

        return modalRef;
    }

    private generateGroupHeaderId() {
        return `groupHeader_${new Date().getTime()}`;
    }

    private createNewGroupHeader(rn: RowNode, title: string, child = false) {
        const ghid = this.generateGroupHeaderId();
        const data = { id: ghid, title, groupHeader: true, halign: 'left', fixed: 'UNFIXED' };
        const gh = {
            data,
            children: [],
            expanded: true,
            visible: true,
            icon: this.GROUP_HEADER_ICON,
        };
        if (!child) {
            if (!rn.parent) {
                const i = this.treeData.findIndex(n => n.data.id === rn.id) + 1;
                this.treeData.splice(i, 0, gh);
                this.treeData = this.treeData.slice();
            } else {
                gh.data.fixed = rn.parent.data.fixed;
                this.tt.appendChildren([gh], rn.parent);
            }
        } else {
            gh.data.fixed = rn.node.data.fixed;
            this.tt.appendChildren([gh], rn.node);
        }
    }

    // 
    removeGroup(event: MouseEvent, rowNode) {
        if (this.tt.selectedRow && rowNode.id === this.tt.selectedRow.id) {
            this.nodeSelected.emit(null);
        }
        const childs = this.tt.serializedValue.filter(n => n.parents.indexOf(rowNode.id) > -1 && !n.node.data.groupHeader).map(n => n.node);
        this.treeData = this.treeData.filter(n => n.data.id !== rowNode.id);

        this.tt.remove(rowNode.id);
        this.tt.appendChildren(childs, this.getHouXuanNode().node);
    }

    // 
    addNewGroup(rn: RowNode, child = false) {
        const modalRef = this.showNewHeaderDialog(() => {
            const ghfRef = modalRef.content as NewHeaderFormComponent;
            if (ghfRef.title) {
                if (!rn || rn.id === this.HOU_XUAN_LIE_ID) {
                    this.treeData.splice(0, 0, {
                        data: {
                            id: this.generateGroupHeaderId(), halign: 'left',
                            title: ghfRef.title, groupHeader: true, fixed: 'UNFIXED'
                        },
                        icon: this.GROUP_HEADER_ICON,
                        children: []
                    });
                    this.tt.updateSerializedValue();
                } else {
                    this.createNewGroupHeader(rn, ghfRef.title, child);
                }

                modalRef.close();
            } else {
                this.notitySer.info(this.languageRes.required);
                ghfRef.input.nativeElement.focus();
            }
       }, child? this.languageRes.contextMenus.newChild: this.languageRes.contextMenus.new);

    }

    // 
    onMoreBtnClick($event: MouseEvent, nodeData) {
        if (nodeData && nodeData.id) {
            this.tt.selectNode(nodeData.id);
        }
        this.tt.ctxMenu.onContextMenu($event);
    }

    private getCanSelectColumns(rowNode: RowNode) {
        // 
        //  + 

        const hxl = this.getHouXuanNode();
        let fields = rowNode ? rowNode.node.children : [];
        fields = fields.filter((n: TreeNode) => !n.data.groupHeader)
        return hxl.node.children.concat(fields).map(n => n.data);
    }

    private showSetColumnsDialog(okCallback, selectedItems, rowNode: RowNode) {
        const modalRef = this.modalService.show(SetColumnsComponent, {
            title: this.languageRes.contextMenus.setDataField,
            width: 600,
            height: 500,
            showMaxButton: false,
            resizable: false,
            initialState: {
                data: this.getCanSelectColumns(rowNode),
                checkedItems: selectedItems,
                groupEditorRef: this
            },
            buttons: [
                {
                    text: this.languageRes.cancel,
                    cls: 'btn btn-secondary',
                    handle: () => {
                        modalRef.close();
                    }
                },
                {
                    text: this.languageRes.ok,
                    cls: 'btn btn-primary',
                    handle: () => {
                        okCallback(modalRef.content.selectedIds)
                    }
                }
            ]
        });

        return modalRef;
    }

    /**  */
    setColumnsForGroupHeader($event, rowNode: RowNode) {
        const childs = rowNode ? rowNode.node.children ? rowNode.node.children.map(n => n.data) : [] : [];
        const modalRef = this.showSetColumnsDialog((selectedIds: string[]) => {
            // 
            const cols = selectedIds.map(n => {
                return this.fields.find(f => f.field == n);
            }).filter(n => n);
            // 
            const hxl = this.getHouXuanNode();
            // 
            hxl.node.children = (hxl.node.children || []).filter(n => cols.findIndex(c => c.field == n.id) === -1);

            // 
            const needRemoveNodes = (rowNode.node.children || []).filter( (n) => {
                return !n.data.groupHeader && cols.findIndex((c) => c.field === n.id) == -1
            }).filter(n => n);

            if (needRemoveNodes && needRemoveNodes.length) {
                needRemoveNodes.forEach(n => {
                    this.tt.remove(n.id);
                });
            }

            // 
            const needAppendNodes = cols.filter(n =>  (rowNode.node.children || []).findIndex(c => c.id === n.field) === -1).map(n => {
                if (!n.id) {
                    n.id = n.field;
                }
                return n;
            });
            if (needAppendNodes && needAppendNodes.length) {
                needAppendNodes.forEach(n => {
                    const col = this.fields.find(f => f.field == n.field);
                    if (col) {
                        col.fixed =  rowNode.node.data.fixed;
                    }
                });

                this.tt.appendChildren(needAppendNodes.map(n => {
                    return {
                        data: n,
                        children: [],
                        expanded: true
                    };
                }), rowNode.node);
    
                this.tt.expandNode(rowNode.id);
            }

            // 
            const hxlCols = needRemoveNodes.map(n => {
                return {
                    data: n.data,
                    children: []
                };
            });
            this.tt.appendChildren(hxlCols, hxl.node);

            modalRef.close();
        }, childs, rowNode);
    }

    // 
    setColumnHeader(event: MouseEvent, col) {
        const i = this.treeData.length - 1;
        this.treeData.splice(i, 0, {  data: col, children: [] });

        const hxl = this.getHouXuanNode();
        if (hxl) {
            hxl.node.children = hxl.node.children.filter(n => n.data.id !== col.id);
            hxl.node.leaf = !(hxl.node.children && hxl.node.children.length);
            if (hxl.node.leaf) {
                hxl.node.icon = this.GROUP_HEADER_ICON;
            }
            this.tt.clearSelections();
            this.tt.updateSerializedValue();
            this.tt.selectNode(col.id, true);
        }
        
    }
    
    moveUp(event: MouseEvent, rowNode) {
        let data = this.treeData;
        if (rowNode.parent) {
            data = rowNode.parent.children;
        }

        const index = data.findIndex(n => n.data.id === rowNode.id);
        if (index !== 0) {
            const tempArr = data.splice(index, 1);
            data.splice(index - 1, 0, ...tempArr);

            this.tt.updateSerializedValue();
        }
    }

    moveDown(event: MouseEvent, rowNode) {
        let data = this.treeData;
        if (rowNode.parent) {
            data = rowNode.parent.children;
        }
        const index = data.findIndex(n => n.data.id === rowNode.id);
        const len = data.length;

        if (index !== len - 2) {
            const tempArr = data.splice(index, 1);
            data.splice(index + 1, 0, ...tempArr);
            this.tt.updateSerializedValue();
        } else {
            this.moveBottom(event, rowNode);
        }
    }

    moveTop(event: MouseEvent, rowNode) {
        let data = this.treeData;
        if (rowNode.parent) {
            data = rowNode.parent.children;
        }
        const index = data.findIndex(n => n.data.id === rowNode.id);
        data.unshift(rowNode.node);
        data.splice(index + 1, 1);
        this.tt.updateSerializedValue();
    }

    moveBottom(event: MouseEvent, rowNode) {
        let data = this.treeData;
        if (rowNode.parent) {
            data = rowNode.parent.children;
        }

        const index = data.findIndex(n => n.data.id === rowNode.id);
        data.push(rowNode.node);
        data.splice(index, 1);

        if (!rowNode.parent) {
            this.moveUp(event, rowNode);
        } else {
            this.tt.updateSerializedValue();
        }
    }

    /**  */
    getPreviewGroupColumns() {
        const treeNodes = this.getAllNodes();
        return this.convert2Array(treeNodes); 
    }

     /**  */
     getHouXuanFields() {
        const hxl = this.treeData.find(n => n.data.houxuanquNode);
        if (hxl) {
            return hxl.children;
        } else {
            return [];
        }
    }

    private getAllNodes() {
        const hxl = this.getHouXuanFields();
        return this.tt.serializedValue.filter(n => {
            return !n.node.data.houxuanquNode && hxl.findIndex(k => k.data.id === n.id) === -1;
        });
    }

    /**   */
    convert2Array(rowNodes: RowNode[]) {
        const r = [];
        const maxBy = (arr, fn) => Math.max(...arr.map(typeof fn === 'function' ? fn : val => val[fn]));
        const maxlevel = maxBy(rowNodes, (n) => n.level) ;
        // 
        rowNodes = rowNodes.filter(n => (n.node.children&& n.node.children.length && n.node.data.groupHeader) || !n.node.data.groupHeader);

        /**
         * 1
         * 2
         * 3
         */
        const parents = rowNodes.filter(e => !e.parent);
        if (!parents.filter(n => !n.node.data.fixed || n.node.data.fixed === 'UNFIXED').length) {
            const leftCols = parents.filter(n => n.node.data.fixed === 'left');
            const rightCols = parents.filter(n => n.node.data.fixed === 'right');
            if (leftCols.length == parents.length || rightCols.length == parents.length) {
                rowNodes.forEach(n => {
                    if (parents.find(p => p.id === n.id)) {
                        n.node.data.fixed = ''
                    }
                });
            } else {
                if (rightCols.length === 1) {
                    leftCols.forEach(t =>{
                        rowNodes.forEach(n => {
                            if (t.id === n.id) {
                                n.node.data.fixed = ''
                            }
                        });
                    });
                } else {
                    rightCols.forEach(t =>{
                        rowNodes.forEach(n => {
                            if (t.id === n.id) {
                                n.node.data.fixed = ''
                            }
                        });
                    });
                }
            }
        }


        for (let i = 0; i <= maxlevel; i++ ) {
            r.push(rowNodes.filter(n => n.level === i).map(n => {
                const data = n.node.data;
                const _fixed = n.parent ? this.tt.getRoot(n.id).node.data.fixed :  (data.fixed || '');


                const _colspan = (n.node.children ? rowNodes.filter(k => k.parents.indexOf(n.id) > -1 && k.id.indexOf('groupHeader_') == -1).length : 1) || 1;
                const _rowspan = (n.node.children && n.node.children.length > 0) ? 1 : (maxlevel - n.level + 1);

                const newCol: any = {
                    field: data.field,
                    title: data.title,
                    sortable: data.sortable,
                    order: data.order,
                    halign: data.halign || 'left',
                    align: data.align || 'left',
                    visible: true,
                    allowGrouping: data.allowGrouping,
                    groupFooter: data.groupFooter,
                    footer: data.footer,
                    width: (data.size ? data.size.width : 100),
                    dataType: data.dataType || 'string',
                    fixed: _fixed === 'UNFIXED'? '': _fixed,
                    id: n.id,
                    parentId: n.node.parent ? n.node.parent.id : null,
                    rowspan: _rowspan,
                    colspan: _colspan
                };

                const newCol2: any = {
                    field: data.field,
                    title: data.title,
                    halign: data.halign || 'left',
                    align: data.align || 'left',
                    visible: true,
                    groupFooter: {},
                    footer: {},
                    width: (data.size ? data.size.width : 100),
                    dataType: data.dataType || 'string',
                    fixed: _fixed === 'UNFIXED'? '': _fixed,
                    id: n.id,
                    parentId: n.node.parent ? n.node.parent.id : null,
                    rowspan: _rowspan,
                    colspan: _colspan
                };

                return n.node.data.groupHeader ?  newCol2: newCol;
            }));
        }

        return r;
    }

    onBeforeShowContextMenu = (t) => {
        if (t && t.data.parents.indexOf(this.HOU_XUAN_LIE_ID) > -1) {
            // 
            return of({show: false});
        }
        return of({show: true});
    }


    private clearTreeNodesData(nodes: TreeNode[]) {
        nodes.forEach((n: any) =>{
            delete n.parent;
            delete n.elementRef;
            if (n.children && n.children.length) {
                this.clearTreeNodesData(n.children);
            }
        })
    }

    getSettings() {
        this.clearTreeNodesData(this.treeData);
        return {
            viewCols: this.getPreviewGroupColumns(),
            treeData: this.treeData
        };
    }

    hiddenCol($event: MouseEvent, id) {
        $event.stopPropagation();
        
        if (this.tt.selectedRow && id === this.tt.selectedRow.id) {
            this.tt.clearSelections();
            this.nodeSelected.emit(null);
        }

        this.tt.remove(id);

    }

    resetHeaderName($event: MouseEvent, rowNode: RowNode) {
        const modalRef = this.showNewHeaderDialog(() => {
            const ghfRef = modalRef.content as NewHeaderFormComponent;
            if (ghfRef.title) {
                rowNode.node.data.title = ghfRef.title;
                modalRef.close();
            } else {
                this.notitySer.info(this.languageRes.required);
                ghfRef.input.nativeElement.focus();
            }
       }, this.languageRes.contextMenus.rename);

       const ghfRef = modalRef.content as NewHeaderFormComponent;
       ghfRef.title = rowNode.node.data.title;
    }
    
}