import { Directive, ElementRef, OnInit, Input, Output, EventEmitter, OnDestroy } from "@angular/core";
import { Subscription, fromEvent, race } from 'rxjs';
import { buffer, bufferCount, debounceTime, filter, first, map, repeat } from 'rxjs/operators';


@Directive({
    selector: '[f-dbl-click]'
})
export class FarrisDblClickDirective implements OnInit, OnDestroy {

    @Input('f-dbl-click') options;
    @Output() dblClickHandler: EventEmitter<any> = new EventEmitter();

    private _click$: Subscription  = null;
    constructor(private el: ElementRef) {}

    ngOnInit(): void {
        const doubleClickDuration = 250;
        const leftClick$ = fromEvent(this.el.nativeElement, 'click').pipe(filter((event: any) => event.button === 0));
        const debounce$ = leftClick$.pipe(debounceTime(doubleClickDuration));
        const clickLimit$ = leftClick$.pipe(bufferCount(2));
        const bufferGate$ = race(debounce$, clickLimit$)
        .pipe(
          first(),
          repeat(),
        );

        this._click$ = leftClick$.pipe(
            buffer(bufferGate$),
            filter(clicks => {
                return clicks.length > 1;
            })
        ).subscribe(clicks => {
            this.dblClickHandler.emit(clicks[0]);
        });
    }

    ngOnDestroy() {
        if (this._click$) {
            this._click$.unsubscribe();
            this._click$ = null;
        }
    }
}